<template >
  <div class="image-modal-bg">
    <div class="image-modal-content">
      <button class="button image-modal-content is-pulled-right" @click="$emit('close')">Close</button>
      <div v-if="loading">Loading</div>
      <div v-else>
        <div v-for="(img, index) in images" class="imgWr" @click="selectImage(index)">
          <img  :src="imageUrl(index)" alt="">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    components: {
    },

    created: function () {
      this.getImage(1)
      console.log(this.image)
    },
    props: {
      visible: Boolean,
      image: Object
    },

    data () {
      return {
        loading: true,
        images: []
      }
    },

    computed: {

    },

    methods: {
      close () {
        this.$emit('close')
      },
      getImage (page) {
        let baseUrl = 'http://vnguy.com'
        this.$http.get(baseUrl + '/image/get/' + page)
          .then(res => {
            this.loading = false
            this.images = res.data
          }).catch(err => {
            console.log(err)
          })
      },
      imageUrl: function (index) {
        return 'http://vnguy.com/image/' + this.images[index].path
      },
      selectImage (index) {
        this.$emit('selected', this.images[index])
        this.$emit('close')
      }
    }

  }
</script>

<style>
  .image-modal-bg{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: 5000;
  }
  .image-modal-content {
    /*margin-top: 5%;*/
  }
  .image-modal-content .imgWr {
    width: 320px;
    height: 100px;
    display: inline-block;
    border: 1px solid #ddd;
    cursor: pointer;
  }
</style>



